.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-60%, -15%);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(60%, -15%) scaleX(-1);
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 60%;
		transform: translate(-10%, 0%);
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -20%);
			.orn-5 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(10%, 40%);
				.orn-6 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 60px;
					transform: translate(20%, 10%);
					.orn-7 {
						position: absolute;
						bottom: 0;
						right: 0;
						width: 80px;
						transform: translate(80%, 0);
					}
				}
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 70%;
		transform: translate(30%, 10%);
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-10%, -10%) scaleX(-1);
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 40%;
			width: 80px;
			transform: translate(0, 30%) scaleX(-1);
			.orn-11 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 60px;
				transform: translate(100%, 10%);
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-40%, 10%);
	}
	.orn-13 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-40%, 10%);
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto 2rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px 500px 20px 20px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		background-color: var(--color-primary);
		border-radius: 500px 500px 20px 20px;
		padding: 2rem;
		.thank {
			position: relative;
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
