.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	.orn-1 {
		position: absolute;
		top: 40%;
		left: 0;
		width: 100px;
		transform: translate(-70%, -50%) rotate(20deg);
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(40%, 40%);
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 120px;
		transform: translate(70%, -50%) rotate(-20deg) scaleX(-1);
		.orn-4 {
			position: absolute;
			right: 0;
			bottom: 0;
			width: 100px;
			transform: translate(40%, 75%);
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
