.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-60%, -15%);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(60%, -15%) scaleX(-1);
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 60%;
		transform: translate(-10%, 0%);
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -20%);
			.orn-5 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(10%, 40%);
				.orn-6 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 60px;
					transform: translate(20%, 10%);
					.orn-7 {
						position: absolute;
						bottom: 0;
						right: 0;
						width: 80px;
						transform: translate(80%, 0);
					}
				}
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 70%;
		transform: translate(30%, 10%);
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-10%, -10%) scaleX(-1);
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 40%;
			width: 80px;
			transform: translate(0, 30%) scaleX(-1);
			.orn-11 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 60px;
				transform: translate(100%, 10%);
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-40%, 10%);
	}
	.orn-13 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-40%, 10%);
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: grid;
			grid-template-rows: auto 1fr;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 6rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
